<template>
  <div>
    <van-nav-bar fixed title="收货地址" left-arrow @click-left="$router.go(-1)"/><br><br>
    <div v-if="addressList.length > 0">
      <van-address-list v-model="chosenAddressId" :list="longAddress" default-tag-text="默认" @add="onAdd" @edit="onEdit"/>
    </div>
  <div class="empty-address" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
        您还没有设置收货地址哦，快去设置吧
      </div>
      <div class="btn" @click="$router.replace('/newAddress')">添加地址</div>
  </div>
  </div>

</template>

<script>
// import { Toast } from 'vant'
import { getAddressApi, getDefaultIdApi, setDefaultIdApi } from '@/api/address'
export default {
  name: 'MyAddressIndex',
  data () {
    return {
      chosenAddressId: 1,
      addressList: []
    }
  },
  created () {
    this.$toast.loading({
      message: '加载中...',
      forbidClick: true,
      loadingType: 'spinner',
      duration: 300
    })
    this.getAddresslist()
  },
  computed: {
    // 详细地址
    longAddress () {
      return this.addressList.map(item => {
        return {
          id: item.address_id,
          name: item.name,
          tel: item.phone,
          address: item.region.province + ' ' + item.region.city + ' ' + item.region.region + ' ' + item.detail,
          isDefault: item.isDefault // 默认地址
        }
      })
    }
  },
  methods: {
    onAdd () {
      this.$router.push('/newAddress')
    },
    onEdit (e) {
      this.$router.push({
        path: '/newAddress',
        query: {
          id: e.id,
          mode: '修改地址',
          isDelete: false
        }
      })
    },

    // 获取收货地址列表
    async getAddresslist () {
      // 获取收货地址列表
      const res = await getAddressApi()
      const { data: { list } } = res
      // 获取默认收货地址Id
      const { data: { defaultId } } = await getDefaultIdApi()
      // 找出对应的默认收货地址
      const defaultAds = list.find(item => item.address_id === defaultId)
      // 在其中添加一个isDefault = true
      Object.assign(defaultAds || list[0] || {}, { isDefault: true }) // 对象中添加键值对
      // 没有默认收货地址Id就让第一个为默认地址
      if (!defaultAds) {
        setDefaultIdApi(list[0].address_id)
      }
      this.chosenAddressId = (defaultAds || list[0] || {}).address_id
      this.addressList = list
    }
  }
}
</script>

<style lang="less" scoped>

.address{
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;

  .address-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;

    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }

    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
}
}

.empty-address {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
